@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Romanesco&display=swap');
.App {
  text-align: center;
}

.logo {
  
font-family: 'Romanesco',sans-serif;
font-weight: 500;
color: azure;
margin: .5% 7%;
padding: 9px 20px;
border-radius: 50%;
border: solid azure;

animation: binksake 2s infinite;
transition: .3s ease-in-out;
}

@keyframes binksake {
  0% {
    background-color: none;
  }
  50% {
   background-color:#000d53;
  }
  100% {
    background-color: none;
  }
}
.learn{
  border: solid black;
  padding: 0.3em 2%;
  color: black;
  box-sizing: border-box;
}
